.about {
  padding-top: 75px;
  margin-bottom: 110px;

  @media (max-width: $width-tablet) {
    padding-top: 27px;
    margin-bottom: 82px;
  }

  @media (max-width: $width-mobile) {
    padding-top: 40px;
    margin-bottom: 65px;
  }
}

.about__wrapper {
  padding-left: 180px;
  position: relative;

  background-image: url("../img/about.png");
  background-repeat: no-repeat;
  background-position: -30px 0;

  @media (max-width: $width-tablet) {
    padding-left: 185px;
    background-position: -32px 0;
  }

  @media (max-width: $width-mobile) {
    padding-left: 0;
    background: none;
  }
}

.about__wrapper h2 {
  font-family: $font-roboto;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 0;
  margin: 0;

  @media (max-width: $width-tablet) {
    font-size: 32px;
    line-height: 140.62%;
    margin-bottom: 26px;
  }

  @media (max-width: $width-mobile) {
    font-size: 28px;
    line-height: 140.62%;
    margin-bottom: 21px;
  }
}

.about__wrapper p {
  font-family: $font-ptmono;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 20px;
  margin-top: 20px;
  padding-right: 20px;

  @media (max-width: $width-tablet) {
    margin-top: 0;
    margin-bottom: 20px;
    margin-left: 5px;
    padding-right: 11px;
  }

  @media (max-width: $width-mobile) {
    margin-left: 0;
    padding-right: 0;
    margin-bottom: 40px;
  }
}

.about__link {
  margin-top: 20px;
  padding: 14px 50px;

  @media (max-width: $width-tablet) {
    margin-top: 20px;
  }

  @media (max-width: $width-mobile) {
    margin-top: -13px;
  }
}

.about__hidden {
  @media (max-width: $width-tablet) {
    display: none;
  }
}
